/* Write your CSS here */

.app-container {
    /* padding: 10px; */
    min-height: 60vh;
    /* min-width: 120vh; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* background-image: linear-gradient(
      to bottom,
      #ffffff,
      #cffcf1,
      #cffcf1,
      #defafe
    ); */
  }
  
  .header-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  
  .timer-heading {
    font-size: 35px;
    font-weight: 600;
    font-family: 'Roboto';
    color: #000000;
    justify-content: center;
    align-items: center;
  }
  
  .display-timer-container {
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
    /* width: 100%; */
    /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
  }
  
  @media screen and (min-width: 768px) {
    .display-timer-container {
      max-width: 1000px;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
    }
  }
  
  .show-time-container {
    /* background-image: url('https://assets.ccbp.in/frontend/react-js/digital-timer-elapsed-bg.png'); */
    /* background-size: cover; */
    /* background-position: center; */
    /* height: 500px; */
    /* width: 100%; */
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
  }
  
  @media screen and (max-width: 767px) {
    .show-time-container {
      width: 100%;
    }
  }
  @media screen and (min-width: 768px) {
    .show-time-container {
      width: 60%;
      max-width: 500px;
    }
  }
  .timer-block {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 200px;
    /* width: 150px;
    height: 150px; */
    /* display: flex;
    flex-direction: column; */
    justify-content: center;
    align-items: center;
  }
  .time-text {
    font-size: 35px;
    font-weight: 600;
    font-family: 'Arial';
    text-align: center;
    color: #00d9f5;
    margin-bottom: 0px;
    padding-bottom: 0px;
  }
  .time-status {
    color: black;
    text-align: center;
    font-weight: 600;
    /* font-family: 'Roboto'; */
    font-size: 20px;
  }
  
  .timer-control-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  @media screen and (min-width: 768px) {
    .timer-control-container {
      width: 100%;
    }
  }
  .control-options-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .start-stop-container {
    display:flex;
    flex-direction:row;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
  }
  
  .start-stop-button {
    background-color: transparent;
    border: none;
    outline: none;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .start-stop-img,
  .reset-img {
    width: 50px;
    height: 50px;
    padding: 10px;
  }
  @media screen and (min-width: 768px) {
    .start-stop-img,
    .reset-img {
      width: 60px;
      height: 60px;
      padding: 10px;
    }
  }
  .start-stop-text {
    font-size: 15px;
  }
  @media screen and (min-width: 768px) {
    .start-stop-text {
      font-size: 25px;
    }
  }
  
  .set-timer-controls {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .set-limit-heading-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 0px;
    padding-bottom: 0px;
  }
  .set-timer-heading {
    font-size: 20px;
    font-weight: 600;
    font-family: 'Roboto';
    margin-bottom: 0px;
    padding-bottom: 0px;
  }
  .inc-dec-time-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 0px;
    padding-top: 0px;
  }
  
  .inc-sec-button {
    background-color: transparent;
    border: none;
    outline: none;
    padding: 10px;
  }
  .time-limit-text-container {
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to right, #00f5a0, #00d9f5);
    width: 90px;
    height: 50px;
    padding: 0px;
    border-radius: 8px;
    color: #ffffff;
  }
  .time-limit-text {
    font-size: 30px;
    font-weight: 600;
    font-family: 'Roboto';
  }
  .inc-dec-icon {
    font-size: 50px;
    cursor: pointer;
  }
  